.mint-cell {
  position: relative;
  position: relative;
  background-color: #fff;
  box-sizing: border-box;
  color: inherit;
  min-height: 48px;
  display: block;
  overflow: hidden;
}
.mint-cell img {
  vertical-align: middle;
}
.mint-cell::after {
  color: #d9d9d9;
  content: " ";
  width: 100%;
  height: 1;
  border-top: 1px solid;
  top: 0;
  left: 0;
  position: absolute;
  transform-origin: 0 0;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
  .mint-cell::after {
    transform: scaleY(.5);
  }
}
.mint-cell::before {
  color: #d9d9d9;
  content: " ";
  width: 100%;
  height: 1;
  border-bottom: 1px solid;
  bottom: 0;
  left: 0;
  position: absolute;
  transform-origin: 0 100%;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
  .mint-cell::before {
    transform: scaleY(.5);
  }
}
.mint-cell + .mint-cell::after {
  content: none;
}
.mint-cell::before {
  left: 10px;
}
.mint-cell:last-child::before {
  left: 0;
}
.mint-cell-wrapper {
  -ms-flex-align: center;
  align-items: center;
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 14px;
  line-height: 1;
  min-height: inherit;
  overflow: hidden;
  padding: 0 15px;
  position: relative;
  width: 100%;
}
.mint-cell-mask {}
.mint-cell-mask::after {
  background-color: #000;
  content: " ";
  opacity: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  position: absolute;
}
.mint-cell-mask:active::after {
  opacity: .1;
}
.mint-cell-text {
  vertical-align: middle;
}
.mint-cell-label {
  color: #888;
  display: block;
  font-size: 12px;
  margin-top: 6px;
}
.mint-cell-value {
  color: #888;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}
.mint-cell-value.is-link {
  margin-right: 24px;
}
.mint-cell-left {
  position: absolute;
  height: 100%;
  left: 0;
  transform: translate3d(-100%, 0, 0);
}
.mint-cell-right {
  position: absolute;
  height: 100%;
  right: 0;
  top: 0;
  transform: translate3d(100%, 0, 0);
}
.mint-cell-allow-right::after {
  border: solid 2px #c8c8cd;
  border-bottom-width: 0;
  border-left-width: 0;
  content: " ";
  top: 50%;
  right: 20px;
  position: absolute;
  width: 8px;
  height: 8px;
  transform: translateY(-50%) rotate(45deg);
}
